html {
  font-size: 16px;
  background: $black;
}

body {
  width: 100%;
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  color: $white;
  line-height: 1.5;
}

@font-face {
  font-family: 'XXII DaemonRunes';
  src: url('../assets/fonts/XXIIDaemonRunes.woff2') format('woff2'),
       url('../assets/fonts/XXIIDaemonRunes.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

a {
  text-decoration: none;
  color: $main;
}

::-webkit-scrollbar {
    width: 12px
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.7);
    border-radius: 10px
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.9)
}

.container {
  position: relative;
  margin: 0 auto;
}

.container-inner {
  position: relative;
  max-width: 1120px;
  margin: 0 auto;
}

.container-mini {
  max-width: 900px;
  margin: 0 auto;
}

.logo {
  display: block;
  max-width: 100%;
}

.title {
  @include DR;
  letter-spacing: .2rem;
  margin-bottom: 50px;
  padding: 10px;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  font-size: 72px;
  @include tablet {
    font-size: 46px;
  }
}

h2 {
  font-size: 24px;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $main;
  border: 2px solid $main;
  padding: 20px 36px;
  white-space: nowrap;
  text-transform: uppercase;
  color: $white;
  text-align: center;
  transition: all .3s;
  cursor: pointer;
  line-height: 1.2;
}