.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 10px 30px;
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $main;
  @include tablet {
    display: none;
  }
  &__social {
    cursor: pointer;
    display: flex;
    &:hover .social {
      display: block;
    }
  }
}

.social {
  display: none;
  margin-left: 20px;
  &__list {
    display: flex;
  }
  &__item {

  }
  &__link {
    display: block;
  }
}