.main {
  position: relative;
  min-height: 100vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    padding: 80px 20px;
    width: 100%;
    height: 100%;
    overflow: auto;
  }
}

.home {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 100%;
  @include tablet {
    flex-direction: column;
  }
  &__block {
    width: 50%;
    @include tablet {
      width: 100%;
    }
  }
  &__title {
    margin-bottom: 50px;
  }
  &__album {
    width: 400px;
    margin: 0 auto;
    background: $main;
    box-shadow: $shadow-down;
    overflow: hidden;
    @include tabletL {
      width: 300px;
    }
    @include tabletM {
      width: 220px;
    }
  }
  &__album-link {
    display: block;
    height: 0;
    position: relative;
    padding-bottom: 100%;
  }
  &__album-img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    object-fit: cover;
  }
}

.about {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &__container {
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }
  &__block {
    width: 40%;
    display: flex;
    align-items: center;
    @include tablet {
      width: 100%;
    }
  }
  &__descr {
    padding: 10px;
  }
}

.gallery {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &__container {
    display: flex;
    overflow: auto;
    height: 250px;
    padding-bottom: 15px;
  }
  &__img {
    object-fit: cover;
    height: 100%;
    margin-right: 29px;
    &:last-child {
      margin-right: 0;
    }
    @include mobileL {
      max-width: 280px;
    }
  }
}