.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  padding: 10px 30px;
  z-index: 100;
  &__wrapper {
    @include tablet {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: $black;
    }
  }
  &__logo {
    width: 60px;
  }
}

.header-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include tablet {
      display: none;
    }
  }
  &__item {
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    display: block;
  }
}

.header-menu {
  &-btn {
    display: none;
    padding: 10px;
    opacity: .5;
    @include tablet {
      display: flex;
    }
  }
}